:root {
  --colorDarkRed: rgba(126, 24, 27, 1);
  --colorDarkGray: rgba(110, 110, 110, 1);
  --colorGray: rgba(232, 232, 232, 1);
  --colorLightGray: rgba(240, 240, 240, 1);
  --colorRed: rgb(192, 6, 0);
  --colorRed2: #e3403a;
  --colorRed3: #dfc1c0;
  --colorWhite: rgba(255, 250, 250, 1);  
  --colorGrayGradient: radial-gradient(circle, rgb(111, 113, 114) 0%, rgb(30, 33, 36) 100%);
  --colorRedGradient: radial-gradient(circle, rgba(159,5,0,1) 0%, rgba(126,24,27,1) 100%);
  /* --colorGrayGradient: radial-gradient(circle, rgb(111, 113, 114) 0%, rgb(44, 49, 53) 100%); */
  /* --colorGrayGradient: radial-gradient(circle, rgba(255,255,255,1) 0%, rgb(125, 125, 125) 100%); */
}


body,
html {
  font-family: "Open Sans", sans-serif;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  color: #777;
  font-weight: 400;
  width: 100% !important;
  height: 100% !important;
}
h2,
h3,
h4 {
  font-family: "Raleway", sans-serif;
}
h2 {
  text-transform: uppercase;
  margin: 0 0 20px;
  font-weight: 800;
  font-size: 36px;
  color: #333;
}
h3 {
  font-size: 20px;
  font-weight: 600;
  color: #333;
}
h4 {
  font-size: 18px;
  color: #333;
  font-weight: 600;
}
h5 {
  text-transform: uppercase;
  font-weight: 700;
  line-height: 20px;
}
p {
  font-size: 15px;
}
p.intro {
  margin: 12px 0 0;
  line-height: 24px;
}
a {
  color: #608dfd;
  font-weight: 400;
}
a:hover,
a:focus {
  text-decoration: none;
  color: #608dfd;
}
ul,
ol {
  list-style: none;
}
ul,
ol {
  padding: 0;
  -webkit-padding: 0;
  -moz-padding: 0;
}
hr {
  height: 2px;
  width: 70px;
  text-align: center;
  position: relative;
  background: #1e7a46;
  margin-bottom: 20px;
  border: 0;
}
/* Navigation */
#menu {
  padding-top: 15px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  transition: all 0.8s;
  
}
#menu.navbar-default {
  background-color: rgba(192, 6, 0, 0.9);
  border-color: rgba(255, 255, 255, 0);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
}
#menu a.navbar-brand {
  font-family: "Raleway", sans-serif;
  /* font-size: 24px; */
  font-weight: 700;
  color: #333;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}

#menu.navbar-default .navbar-nav > li > a {
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  color: white;
  font-size: 15px;
  font-weight: 600;
  /* padding: 8px 2px; */
  border-radius: 0;
  margin: 0px 20px 0;
}

.navbar-collapse {
  border-color: transparent !important;
}

@media (max-width: 768px) {    
  #menu-items:not(.collapse) li {
    text-align: right;
  }

  #menu-items:not(.collapse) li:first-child {
    border-top: 1px solid white !important;
  }
}

#header-text {
  margin-left: auto;
  margin-right: auto;
  /* width: 100%; */
  height: 100px;
  display: table;
  margin-top: 60px;
}

#header-text > div {
  display: grid;
  display: table;
  width: 250px;
  height: 100%;
  float: left;
}

#header-text span {
  display: inline-block;
  vertical-align: middle;
  /* float: left; */
  font-size: 15px;
  color: white;
  width: 170px;
  /* height: 100px; */
  font-size: 16px;
  font-weight: 700;  
  opacity: 0.6;
  /* text-align: center; */
  /* line-height: 100px; */
}

#header-text span:first-child {
  font-size: 80px;
  color: white;
  width: 65px;
  font-weight: 500;  
  transform: translateY(0px);
  opacity: 0.7;
  /* float: right; */
}

#nav-logo {
  height: 60px;
}

@media (max-width: 1202px) {  
  #header-text {
    width: 750px;
  }
}

@media (max-width: 992px) {  
  .nav > li > a {
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-left: 10px !important;
    margin-right: 20px !important;
  }
}

@media (max-width: 768px) {    
  #header-text {
    margin-top: 20px;
  }

  #header-text {
    width: 300px;
  }

  #header-text > div {
    width: 250px;
  }
  
  #header-text span {
    font-size: 15px;
  }
  
  #header-text span:first-child {
    font-size: 72px;
  }
}

@media (max-width: 560px) {  
  #header-text {
    margin-top: 00px;
  }
}

@media (max-width: 430px) {  
  #nav-logo {
    height: 50px;
  }
}

.navbar-nav{
  /* padding-top: 10px; */
  transform: translateY(5px);
}

.navbar-nav > li{
  transition: color, background-color 250ms ease-in-out; 
  border-radius: 2px;
  cursor: pointer;
}

.navbar-nav > li:hover {
  background: #e3403a;
  border: 1px solid rgba(#000, .05);
  box-shadow: 1px 1px 2px rgba(#fff, .2);
  text-decoration: none;
  text-shadow: -1px -1px 0 #e3403a;
}

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .active > a:focus {
  background-color: transparent;
}
.navbar-default .navbar-nav > .active > a:after,
.navbar-default .navbar-nav > .active > a:hover:after,
.navbar-default .navbar-nav > .active > a:focus:after {
  display: block !important;
  position: absolute !important;
  left: 0 !important;
  bottom: -1px !important;
  width: 100% !important;
  height: 2px !important;
  /* background: linear-gradient(to right, #6372ff 0%, #5ca9fb 100%) !important; */
  content: "" !important;
  transition: width 0.2s !important;
}
.navbar-toggle {
  border-radius: 0;
  background-color: #fff;
}
.navbar-default .navbar-toggle:hover,
.navbar-default .navbar-toggle:focus {
  background-color: #fff;
  border-color: var(--colorRed2);
}
.navbar-default .navbar-toggle:hover > .icon-bar {
  background-color: var(--colorRed2);
}
.section-title {
  margin-bottom: 70px;
}
.section-title h2 {
  position: relative;
  margin-top: 10px;
  margin-bottom: 15px;
  padding-bottom: 15px;
  font-size: 42px;
  transition: font-size 0.2s ease;
}

@media (max-width: 760px) {
  .section-title h2 {
    font-size: 32px;
  }
}

.section-title h2::after {
  position: absolute;
  content: "";
  height: 4px;
  width: 300px;
  bottom: 0;
  margin-left: -150px;
  left: 50%;
}

.section-title p {
  font-size: 18px;
}

.btn:active,
.btn.active {
  background-image: none;
  outline: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
}
a:focus,
.btn:focus,
.btn:active:focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn.active.focus {
  outline: none;
  outline-offset: none;
}
