/*  */

.call-button{
  width: 300px;
  background-color: #2ecc71;
  display: block;
  color: #fff;
  text-decoration: none;
  margin: 0px auto;
  font-size: 1.5em;
  
  border: none;
  border-radius: 6px;
  padding: 12px 20px;
  font-weight: 600;
  line-height: 22px;
  display: flex;
  align-items: center;

  transition: all 150ms ease-in-out; 

  animation-fill-mode: both;
  -webkit-animation-fill-mode: both;
  -webkit-animation: pulse 1s ease-out;
  animation: pulse 1.8s ease-out;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  text-decoration: none;
}

.call-button:active{
  text-decoration: none !important;
}

.call-button > img{
  margin-right: 2rem;
}

.call-button:hover{
  cursor: pointer;  
  background-color: #00dc5c;
  text-decoration: none;
  color: white;
}

@-webkit-keyframes pulse {
  0% {
  }
  25% {
  }
  50% {
  }
  75% {
  }
  100% {
  }
}

@keyframes pulse {
  0% {
      box-shadow: 0px 0px 0px -2px rgba(154, 237, 202, 0);
  }
  25% {
      box-shadow: 0px 0px 20px -2px rgba(154, 237, 202, 1);
  }
  50% {
      box-shadow: 0px 0px 50px -2px rgba(154, 237, 202, 0.6);
  }
  75% {
      box-shadow: 0px 0px 70px -2px rgba(154, 237, 202, 0.3);
  }
  100% {
      box-shadow: 0px 0px 90px -2px rgba(154, 237, 202, 0);
  }
}


.divider {
  margin: auto;
  width: 80%;
  position: relative;
}

.divider .shadow {
  overflow: hidden;
  height: 6px;
}

.divider .shadow:after {
  content: '';
  display: block;
  margin: -25px auto 0;
  width: 100%;
  height: 25px;
  border-radius: 125px/12px;
  box-shadow: 0 0 4px rgba(255, 255, 255, 1);
}


